#nav-menu {
  border: 1px solid lightgrey;
}
#nav-menu-main {
  width: 90%;
  height: 70px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid lightgrey;
}
#nav-menu-links > * {
  margin-left: 15px;
}


#home {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  border: 1px solid lightgrey;
}
#home > div {
  border: 1px solid lightgrey;
}
#home > div:nth-child(1) {
  width: 50%;
}
#home > div > img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid lightgrey;
}


#about {
  text-align: center;
  margin-top: 25px;
  border: 1px solid lightgrey;
}


#skills {
  margin-top: 50px;
  text-align: center;
  border: 1px solid lightgrey;
}
#skills-main {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 25px;
}
.skills-card {
  padding: 7px 15px;
  border: 1px solid lightgrey;
}


#projects {
  margin-top: 50px;
  text-align: center;
  border: 1px solid lightgrey;
}
#projects-main {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 25px;
}
.project-card {
  padding: 7px 15px;
  border: 1px solid lightgrey;
}


#contact {
  margin-top: 50px;
  text-align: center;
  border: 1px solid lightgrey;
}
#contact-main {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 1px solid lightgrey;
}


#github-stats {
  margin-top: 25px;
  border: 1px solid lightgrey;
}
